.settings {
 text-align: left;
 .ant-checkbox-group-item {
  display: block;
  margin-right: 8px;
  font-size: 18px;
  line-height: 26px;
 }

 .ant-checkbox-wrapper {
  margin-bottom: 10px;
 }

 &__label {
  font-size: 18px;
  line-height: 28px;
  color: #a3a8bb;
  margin-bottom: 14px;
 }
 &__btn {
  margin-top: 55px;
  margin-bottom: 100px;
  .ant-btn {
   padding: 21px 60px;
  }
 }
}

@media screen and (max-width: 479px) {
 .settings {
  &__label {
   font-size: 16px;
   line-height: 26px;
  }
  &__btn {
   margin-top: 45px;
  }
 }
}
