@import "src/utils/style/mixin";

.bg-main {
 background-image: url('../../../common/images/auth/bg-auth.png');
}
.bg-register {
 background-image: url('../../../common/images/auth/bg-register.jpg');
}

.layout-auth {
 text-align: center;
 height: auto;
 background-position: center 0;
 background-repeat: no-repeat;
 background-color: #2351ff;
 padding: 90px 20px 200px;

 &__window {
  max-width: 800px;
  width: 100%;
  margin: 70px auto 0;
  background: #fafbfe;
  box-shadow: 0 4px 14px 2px rgba(35, 81, 255, .06);
  border-radius: 40px;
  padding: 62px 20px 68px;

  &.title {
   margin-bottom: 50px;
  }

  .links {
   max-width: 418px;
   margin: 0 auto;
  }

  .ant-btn {
   width: 100%;

   span {
    font-size: 20px;
    line-height: 28px;
   }
  }

  .ant-form-item-explain.ant-form-item-explain-error {
   color: #f53a3a;
   text-align: right;
  }

  .ant-row {
   display: block;
   max-width: 420px;
   width: 100%;
   margin: 0 auto;
  }

  .ant-col {
   margin: 0 auto;
  }

  .login-form-forgot {
   max-width: 430px;
   margin: 0 auto;
   padding-right: 5px;
   text-align: right;
   font-size: 16px;
   line-height: 20px;
   color: #232526;

   .ant-form-item-control {
    padding-bottom: 0;
   }

   .login-link {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .icon-key {
     margin-right: 10px;
    }
   }
  }

  .ant-input-password {
   .ant-input {
    background: #fff;
    border-radius: 10px;
    padding: 19px 11px;
   }
  }

  .ant-input-affix-wrapper {
   padding: 0;
   border-radius: 10px;
   border: none;
   height: 100%;

   input {
    padding: 6px 0;
    border: none;
   }
  }

  .ant-form-item-label {
   float: left;
   font-size: 18px;
   line-height: 28px;
   color: #a3a8bb;
   margin-bottom: 10px;
  }

  .ant-input-password-icon {
   margin-right: 12px;

   svg {
    width: 20px;
    height: 20px;
   }
  }

  .border-none {
   .ant-input-wrapper {
    border: none;

    .ant-input-group-addon {
     border-radius: 0;
     border: none;
     background: rgba(250, 251, 254, 0.6);
    }
   }

   .ant-form-item-control-input-content {
    border-radius: 0;
    border: none;

    input {
     border: none;
    }
   }
  }

  .ant-form-item .ant-mentions, .ant-form-item textarea.ant-input {
   min-height: 200px;
   background: #fff;
   border: none;
   box-sizing: border-box;
   border-radius: 10px;
   resize: none;
   padding: 14px 20px;
  }

  .ant-input-wrapper {
   border: 1px solid transparent;

   .ant-input-group-addon {
    border-radius: 10px 0 0 10px;
    border: 1px solid transparent;
    background: rgba(250, 251, 254, 0.6);
   }
  }

  .ant-checkbox-wrapper {
   .ant-form-item-control-input-content {
    border: none;
   }
  }

  .ant-input {
   height: 60px;
  }

  .ant-form-item-control-input-content {
   border-radius: 10px;
   border: 1px solid #eef2ff;


   input {
    border: none;
   }
  }

  .ant-form-item-label > label::after {
   display: none;
  }

  .ant-form-item-has-error {
   .ant-form-item-control {
    padding-bottom: 0;
   }
  }

  .ant-form-item {
   &-control {
    max-width: 420px;
    width: 100%;
    border-radius: 10px;
    padding-bottom: 18px;
    text-align: left;

    input {
     border-radius: 10px;
     padding: 19px 11px;
    }
   }
  }

  .ant-select-single.ant-select-show-arrow .ant-select-selection-item, .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
   border: none;
  }

  .input-phone {
   .ant-input-group-addon {
    padding: 0 6px;
   }
   .ant-select-single.ant-select-show-arrow .ant-select-selection-item, .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
    height: auto;
    background: rgba(250, 251, 254, 0.6);
    padding: 4px 0;
    margin-left: 5px;
    width: auto;
    img {
     margin-bottom: 4px;
     border-radius: 50%;
    }
   }
   .ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    width: 120px;
   }
   .ant-select-arrow {
    right: 5px;
   }

   .ant-input-group-addon {
    text-align: left;
    width: 120px;
   }

   .ant-select-selection-item {
    flex: none;
   }
  }
 }

 .ant-checkbox-wrapper {
  line-height: 26px;
  font-size: 18px;
  margin-bottom: 10px;

  a {
   text-decoration: underline;
  }
 }

 .ant-checkbox-inner {
  border-color: #e0e2ea;
  background-color: #e0e2ea;
  border-radius: 6px;
 }

 .ant-checkbox-checked {
  .ant-checkbox-inner {
   background-color: #2351ff;
   border-color: #2351ff;
   border-radius: 6px;
  }
 }

 .ant-checkbox-wrapper {
  &:hover {
   .ant-checkbox {
    border: none;
   }
  }
 }

 .ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
  display: none;
  content: '';
 }

 @include searchAccess;

 .steps {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 260px;
  width: 100%;
  margin: 42px auto 36px;
  position: relative;

  div.current-step {
   background-color: #6081ff;
  }

  .step {
   width: 80px;
   height: 2px;
   background-color: #EDF0FD;
   border-radius: 110px;

   &:after {
    content: '';
    position: absolute;
    left: 0;
    background-color: #6081ff;
    margin-right: 10px;
    width: 80px;
    height: 2px;
    border-radius: 110px;
   }

   &:before {
    content: '';
    position: absolute;
    right: 0;
    background-color: #EDF0FD;
    margin-left: 10px;
    width: 80px;
    height: 2px;
    border-radius: 110px;
   }
  }
 }

 .ant-form-item-has-error {
  .ant-form-item-control-input-content {
   &:not(.ant-form-item-control-input-content.ant-checkbox-wrapper) {
    border: 1px solid #F53A3A;
   }
  }
 }
}

#login_password[type="password"] {
 color: #2351ff !important;
 font-size: 18px;
 line-height: 26px;
}

.ant-select-dropdown {
 width: 120px !important;
 text-align: center;
}

.login {
 h1 {
  margin: 0;
 }

 &-password {
  .ant-form-item-control {
   padding-bottom: 10px;
  }

 }

 .ant-btn {
  margin-bottom: 10px;
 }

 .title {
  font-size: 44px;
  line-height: 50px;
  margin-bottom: 50px;
 }

 .auth {
  &-link {
   max-width: 245px
  }
 }
}

.auth {
 &-link {
  position: relative;
  max-width: 350px;
  width: 100%;
  margin: 0 auto 22px;
  display: flex;
  align-items: center;
  justify-content: center;

  .auth-arrow-icon {
   font-size: 20px;
   line-height: 25px;

   &:before {
    content: '';
    position: absolute;
    right: 0;
    top: 7px;
    width: 14px;
    height: 14px;
    background-image: url('../../../common/images/auth/arrow-right.svg');
    color: #2351ff;
    margin-left: 10px;
   }
  }
 }
}

.mail-not-confirmed {
 max-width: 530px;
 width: 100%;
 margin: 0 auto;

 .title {
  max-width: 500px;
  font-size: 44px;
  line-height: 50px;
  margin-bottom: 30px;
 }

 h1 {
  margin: 0;
 }

 &__text {
  font-size: 18px;
  line-height: 24px;
  color: #a3a8bb;
  margin: 0 auto 50px;
  text-align: center;
 }

 .ant-btn {
  padding: 22px 133px;
  margin-bottom: 22px;
  max-width: 430px;
 }
}

.forgot {
 max-width: 430px;
 width: 100%;
 margin: 0 auto;

 h1 {
  margin: 0;
 }

 .title {
  font-size: 44px;
  line-height: 50px;
  margin-bottom: 50px;
 }

 &-email {
  text-align: left;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 28px;
  color: #a3a8bb
 }

 .ant-input {
  background: #fff;
  border: 1px solid #eef2ff;
  border-radius: 10px;
  padding: 16px 20px 18px;

  &::placeholder {
   font-size: 18px;
  }
 }

 .ant-btn {
  margin-top: 50px;
  margin-bottom: 32px;
 }
}

.second-step__name {
 font-size: 44px;
 line-height: 50px;
 margin: 40px 0 50px;
}

.three-step {
 margin: 0 40px;

 &__title {
  font-size: 44px;
  line-height: 50px;
  margin-bottom: 30px;
 }

 &__text {
  font-size: 18px;
  line-height: 24px;
  color: #a3a8bb;
 }

 &__resources-page {
  font-size: 18px;
  line-height: 24px;
  border-bottom: 1px solid #232526;
 }

 &__video {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: 70px;
  margin-bottom: 60px;

  img {
   max-width: 720px;
   border-radius: 530px;
   cursor: pointer;
  }

  &:before {
   content: '';
   position: absolute;
   top: 36%;
   left: 43%;
   background-image: url('../../../common/images/materials/play.png');
   background-repeat: no-repeat;
   background-position: center;
   width: 120px;
   height: 123px;
   cursor: pointer;
  }
 }

 &__btn {
  margin-top: 50px;
  margin-bottom: 22px;

  button {
   padding: 0 120px;
   max-width: 430px;
   margin: 0 auto;
  }

  img {
   margin-left: 14px;
  }
 }
}

.register {
 h1 {
  margin: 0;
 }

 .title {
  font-size: 44px;
  line-height: 50px;
 }
}

@media screen and (max-width: 991px) {
 .layout-auth {
  &__window {
   textarea.ant-input {
    width: 100% !important;
   }
  }
 }
 .profile,
 .register {
  .select-item {
   margin-bottom: 10px;
   &:last-child {
    margin-bottom: 30px;
   }
  }
 }
}

@media screen and (max-width: 680px) {
 .layout-auth {
  background: none;
  text-align: center;
  padding: 25px 20px 57px;

  .steps {
   display: none;
  }

  &__logo {
   display: flex;
   justify-content: space-between;
   align-items: center;
  }

  &__window {
   box-shadow: none;
   padding: 0;

   form {
    max-width: 420px;
    width: 100%;
    margin: 0 auto;
   }

   .form__link-add {
    width: 22px;
   }

   .login .title {
    font-size: 34px;
    line-height: 43px;
    margin-bottom: 30px;
   }

   .ant-row {
    margin: 0;
   }

   .ant-form-item-label {
    margin-bottom: 0;
   }

   .auth-link .auth-arrow-icon {
    font-size: 18px;
    line-height: 23px;
    margin-bottom: 60px;

    &:before {
     right: 11px;
    }
   }

   .login-form-forgot {
    font-size: 15px;
    line-height: 19px;
   }

   .ant-input {
    font-size: 15px;
    line-height: 19px;
   }

   .ant-select-dropdown {
    width: 100px !important;
    min-width: 100px !important;
   }

   .input-phone .ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector{
    width: 100px;
    .ant-select-arrow {
     right: -25px;
     top: 17px;
     width: 12px;
    }
   }
   //.ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector
   .ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    padding: 0;
   }
   .ant-checkbox + span {
    font-size: 14px;
    line-height: 22px;
   }
  }

  .ham {
   top: -33px;
  }

  .burg-menu__inner {
   .menu-list__item:last-child {
    margin-top: 120px;
   }

   .authentication {
    .ant-btn {
     padding: 12px 41px !important;
     &:last-child {
      margin-right: 0;
     }
    }
   }
  }

  .ant-select-arrow {
   top: 53%;
  }
 }

 .profile .ant-form-item-label label, .layout-auth .ant-form-item-label label {
  font-size: 16px;
  line-height: 26px;
 }
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  height: 50px;
 }
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item .ant-mentions, .profile .ant-form-item textarea.ant-input, .layout-auth .ant-form-item .ant-mentions, .layout-auth .ant-form-item textarea.ant-input,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  width: 280px;
 }
 .register {
  .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker {
   width: 100%;
  }
  .auth-arrow-icon {
   margin-bottom: 90px;
   &:before {
    top: 0;
    width: 0;
    height: 0;
    background: none;
    margin-left: 0;
   }
  }
 }
}

@media screen and (max-width: 479px) {
 .layout-auth {
  .ant-btn {
   padding: 13px 60px;
   span {
    font-size: 18px;
    line-height: 23px;
   }
  }
  .second-step {
   .search-access {
    margin-bottom: 16px;
   }
   .ant-switch {
    min-width: 40px;
    height: 22px;
    margin-right: 20px;

    &-handle {
     width: 18px;
     height: 18px;
    }

    &-checked .ant-switch-handle {
     left: calc(100% - 20px);
    }
   }

   &__wrapper {
    display: flex;
    align-items: center;
   }

   .avatar {
    padding: 40px;
    width: 80px;
    height: 70px;
    left: 0;
    margin: 0 30px 0 0;

    .add-photo {
     bottom: -12px;
     right: 25px;
     padding: 14px;
    }

    .initials {
     font-size: 20px;
     line-height: 22px;
    }
   }

   &__name {
    font-size: 22px;
    line-height: 28px;
   }
  }

  &__window {

   .form__link-add {
    width: 18px;
   }
  }
 }
}
