.partners {
 margin: 120px 60px 200px;

 &__top {
  display: flex;
  align-items: center;
  margin-bottom: 125px;

  &-headers {
   margin-left: 70px;

   &__title {
    font-size: 70px;
    line-height: 48px;
    margin-bottom: 30px;
   }

   &__subtitle {
    font-size: 28px;
    line-height: 38px;
   }
  }
 }

 &__main {
  &-wrapper {
   display: flex;
   align-items: flex-start;
   justify-content: space-between;

   &__top {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 20px;

    &-title {
     font-size: 44px;
     line-height: 50px;
    }

    &-clear {
     color: #a3a8bb;
     cursor: pointer;
    }
   }

   &__left {
    &-filters {
     max-width: 880px;
     display: flex;
     align-items: center;
     flex-wrap: wrap;
     margin-bottom: 25px;
     margin-top: 5px;

     &:not(.mb30) {
      margin-bottom: 60px;
     }
    }

    &-card {
     max-width: 880px;
     width: auto;
     background: #fff;
     box-shadow: 0 8px 20px 6px rgba(35, 81, 255, 0.06);
     border-radius: 40px;
     font-size: 28px;
     line-height: 38px;

     .card-search-partner {
      p {
       margin-bottom: 50px;
      }
     }

     .ant-btn {
      font-size: 22px;
      line-height: 28px;
     }

     .card-can-search-partner {
      h3 {
       font-size: 36px;
       line-height: 50px;
       margin-bottom: 20px;
      }

      p {
       font-size: 18px;
       line-height: 28px;
       margin-bottom: 50px;
      }
     }
    }
   }

   &__filter {
    margin-right: 80px;

    .ant-checkbox-group-item {
     display: block;
     margin-right: 8px;
    }
   }
  }
 }
}

.filter {
 min-width: 290px;
 max-width: 380px;
 width: 100%;
 color: #a3a8bb;
 font-size: 18px;
 line-height: 28px;

 &__placeholder {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
  border-top: 1px solid rgba(163, 168, 187, .2);
  border-bottom: 1px solid rgba(163, 168, 187, .2);

  & + .filter__placeholder {
   border-top: none;
  }
 }

 &__arrow {
  cursor: pointer;
 }

 &__show {
  margin-top: 15px;
  max-height: 210px;
  overflow: auto;
  margin-right: 6px;

  &::-webkit-scrollbar-track {
   border-radius: 10px;
   border: none;
   background-color: transparent;
  }

  &::-webkit-scrollbar {
   width: 4px;
   background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
   border-radius: 60px;
   background-color: #D2D5E0;
  }

  .ant-checkbox-wrapper {
   display: flex;
   margin-bottom: 4px;

   & + .ant-checkbox-wrapper {
    margin: 0 0 4px 0;
   }

   .ant-checkbox {
    margin-top: 5px;
   }
  }

  .ant-input-search {

   .ant-input-wrapper {
    .ant-input-affix-wrapper {
     background: #fff;
     border: 1px solid #eef2ff;
     border-radius: 10px;
     position: relative;

     & > input.ant-input {
      padding: 10px 0;
     }

     &:after {
      content: '';
      position: absolute;
      background-image: url('../../common/images/partners/search.svg');
      width: 20px;
      height: 20px;
      left: 20px;
      top: 20px;
     }
    }
   }
  }

  .ant-input-group-addon {
   display: none;
  }
 }

 .ant-input {
  margin-left: 35px;
 }

 .ant-switch {
  margin-left: 70px;
 }
}
.card-partner {
 justify-content: flex-start;
 &__info-btn__last {
  margin-left: 180px;
 }
}
.card-partner-cell {
 margin-bottom: 30px;
 max-width: 630px;
 width: 100%;

 .email-phone {
  &__cell {
   display: flex;
   align-items: center;
  }

  &__title {
   font-size: 18px;
   line-height: 28px;
   color: #a3a8bb;
   margin-right: 8px;
  }
 }

 &__title {
  font-size: 18px;
  line-height: 28px;
  color: #a3a8bb;
  margin-bottom: 6px;
 }

 &__text {
  font-size: 18px;
  line-height: 26px;

  width: 100%;

  .tags {
   position: relative;

   &:before {
    content: '';
    position: absolute;
    background-image: url('../../common/images/partners/sharp.svg');
    width: 13px;
    height: 13px;
    top: 5px;
    left: -23px;
   }
  }

  .participant {
   position: relative;

   &:before {
    content: '';
    position: absolute;
    background-image: url('../../common/images/partners/done.svg');
    width: 20px;
    height: 20px;
    top: 2px;
    left: -25px;
   }
  }
 }
}

.padding-card {
 padding: 58px 54px 66px 76px;
}

.padding-cart-partner {
 padding: 58px 40px 68px;
}

@media screen and (max-width: 1221px) {
 .padding-card {
  padding: 40px;
 }
 .partners {
  margin: 120px 30px 200px;

  &__main-wrapper__left-card {
   .card-can-search-partner,
   .card-not-found-partner,
   .card-search-partner {
    font-size: 24px;
    line-height: 34px;

    p {
     margin-bottom: 40px;
    }

    .ant-btn {
     font-size: 20px;
     line-height: 26px;
     padding: 18px 34px;
    }
   }
  }
 }
}

@media screen and (max-width: 1040px) {
 .partners {
  margin: 0;

  .flex-center {
   display: block;
  }

  &__top {
   margin-bottom: 40px;

   &-icon {
    margin-left: 20px;

    svg {
     width: 80px;
     height: 80px;
    }
   }

   &-headers {
    &__title {
     display: flex;
     align-items: center;
     font-size: 60px;
     line-height: 48px;
     margin-bottom: 0;
    }

    &__subtitle {
     font-size: 24px;
     line-height: 30px;
    }
   }
  }

  &__main {
   &-wrapper {
    display: block;

    &__filter {
     margin-right: 0;
    }

    &__top {
     background: #EDF0FD;

     &-title {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 12px 20px;
      font-size: 40px;

      img {
       width: 30px;
      }
     }

     &-btn {
      max-width: 380px;
      margin: 30px auto 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .ant-btn {
       font-size: 20px;
       line-height: 26px;
       padding: 18px 34px;
      }
     }
    }

    &__left {
     width: auto;
     margin: 0 0 150px;

     &-card {
      margin: 0 20px;
      text-align: center;
     }

     .card {
      text-align: left;
     }

     &-filters {
      padding: 0 20px;
      max-width: 100%;
      flex-wrap: nowrap;
      overflow-y: hidden;
      overflow-x: auto;

      &::-webkit-scrollbar-track {
       border-radius: 0;
       background-color: transparent;
      }

      &::-webkit-scrollbar {
       width: 0;
       background-color: transparent;
      }

      &::-webkit-scrollbar-thumb {
       border-radius: 0;
       background-color: transparent;
      }
     }
    }
   }
  }

  .filter {
   margin: 0 auto;

   &__placeholder {
    border-top: none;
   }
  }
 }
 .select-item {
  margin-bottom: 0;
  &:last-child {
   margin-bottom: 0;
  }
 }
}

@media screen and (max-width: 991px) {
 .card-partner__info-btn__last {
  margin-left: 0;
 }
}
@media screen and (max-width: 778px) {
 .partners {
  &__top {
   margin-bottom: 30px;

   &-icon {
    margin-left: 20px;

    svg {
     width: 50px;
     height: 50px;
    }
   }

   &-headers {
    &__title {
     font-size: 45px;
     line-height: 48px;
    }

    &__subtitle {
     font-size: 18px;
     line-height: 30px;
    }
   }
  }

  &__main {
   &-wrapper {
    &__top {
     &-title {
      font-size: 30px;

      img {
       width: 25px;
      }
     }

     &-btn {
      .ant-btn {
       min-width: 130px;
       font-size: 18px;
       line-height: 22px;
       padding: 13px 30px;
       margin: 0 20px;

       &.blue {
        margin-left: 0;
       }
      }
     }
    }
   }
  }

  .card {
   & .card-partner {
    padding: 40px;

    &__info {
     width: 100%;
     .card-partner__info-btn {
      display: flex;
     }
    }
   }
  }

  .filter {
   font-size: 16px;
   line-height: 26px;

   &__placeholder {
    margin: 0 20px;

    .filter__arrow {
     img {
      width: 14px;
     }
    }
   }

   &__show {
    .ant-checkbox {
     width: 18px;
     height: 18px;

     & + span {
      font-size: 16px;
      margin-left: 12px;
     }

     &-wrapper {
      margin: 0 20px 4px;
     }
    }
   }

   .ant-switch {
    min-width: 50px;
    height: 26px;

    &-handle {
     width: 22px;
     height: 22px;
    }

    &-checked .ant-switch-handle {
     left: calc(100% - 24px);
    }
   }
  }
 }
}

@media screen and (max-width: 479px) {
 .partners {
  &__top {
   &-icon {
    margin-left: 20px;

    svg {
     width: 40px;
     height: 32px;
    }
   }

   &-headers {
    margin-left: 20px;

    &__title {
     font-size: 34px;
     line-height: 42px;
    }

    &__subtitle {
     font-size: 15px;
     line-height: 22px;
    }
   }
  }

  &__main {
   &-wrapper {
    &__top {
     &-title {
      font-size: 16px;
      line-height: 26px;

      img {
       width: 18px;
      }
     }
    }

    &__left {
     .select-item {
      padding: 10px 20px;

      &__text {
       font-size: 16px;
       line-height: 20px;
      }
     }

     &-card {
      margin: 0 20px;

      .card-can-search-partner,
      .card-not-found-partner,
      .card-search-partner {
       font-size: 15px;
       line-height: 22px;

       .ant-btn {
        padding: 13px 60px;
        display: flex;
        margin: 0 auto;
       }
      }
     }
    }
   }
  }

  .filter {
   .ant-switch {
    min-width: 40px;
    height: 22px;

    &-handle {
     width: 18px;
     height: 18px;
    }

    &-checked .ant-switch-handle {
     left: calc(100% - 20px);
    }
   }
  }

  .card .card-partner {
   padding: 20px;

   &.padding-cart-partner {
    .ant-btn {
     font-size: 18px;
     line-height: 23px;
    }
   }
  }
 }
 .padding-card {
  padding: 20px;
 }
}

@media screen and (max-width: 379px) {
 .partners .card .card-partner__info .card-partner__info-btn {
  display: block;

  .ant-btn {
   display: block;
   padding: 13px 50px;
   width: 240px;
   margin: 0 auto 20px;
  }
 }
}
