.my-cases {
 max-width: 990px;
 width: 100%;

 &__text {
  font-size: 26px;
  line-height: 33px;
  color: #a3a8bb;
  max-width: 490px;
 }

 .case {
  background: #fff;
  box-shadow: 0 4px 14px 2px rgba(35, 81, 255, 0.06);
  border-radius: 40px;
  padding: 40px 30px 30px 60px;
  margin-bottom: 30px;
  min-width: 280px;

  &-top {
   display: flex;
   align-items: center;
   justify-content: space-between;
   max-width: 990px;
   width: 100%;

   &__title {
    max-width: 830px;
    width: 100%;
    font-size: 36px;
    line-height: 50px;
   }

   &__arrow {
    border-radius: 110px;
    position: relative;
    padding: 23px 17px;
    background-color: #edf0fd;
    cursor: pointer;

    .arrow {
     &-up,
     &-down {
      display: flex;
      align-items: center;
      justify-content: center;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      width: 24px;
      height: 12px;
      border-radius: 110px;
     }

     &-up {
      background-image: url('../../../common/images/materials/arrowUp.svg');
     }

     &-down {
      background-image: url('../../../common/images/materials/arrowDown.svg');
     }
    }
   }
  }

  &-feedback {
   &__title {
    font-size: 18px;
    line-height: 24px;
    color: #a3a8bb;
    margin-bottom: 30px;
   }
  }

 }

 .feedback {
  max-width: 880px;
  width: 100%;
  margin-bottom: 30px;
  display: block;

  & + .feedback {
   padding-top: 30px;
   border-top: 1px solid rgba(163, 168, 187, .3);
  }

  &__stars {
   display: flex;
   margin-bottom: 20px;

   div + div {
    margin-left: 10px;
   }
  }

  &__name {
   font-size: 18px;
   line-height: 28px;
   margin-bottom: 10px;
   font-family: Euclid Flex;
   font-weight: 600;

   img {
    margin: 0 0 3px 10px;
   }
  }

  &__text {
   font-size: 16px;
   line-height: 26px;

   &-read-full {
    cursor: pointer;
    margin-left: 5px;
    color: #2351ff;
   }
  }
 }
}

@media screen and (max-width: 991px) {
 .my-cases .case {
  padding: 30px 20px;
  margin-bottom: 30px;

  &-top {
   &__arrow {
    margin-bottom: 10px;
    padding: 14px;

    .arrow {
     &-up,
     &-down {
      width: 14px;
      height: 14px;
     }
    }
   }

   &__title {
    font-size: 30px;
    line-height: 40px;
   }
  }

  &-text {
   font-size: 15px;
   line-height: 22px;
  }

  .file {
   margin-bottom: 26px;
  }
 }
}

@media screen and (max-width: 788px) {
 .my-cases {

  .case {
   &-top {
    &__title {
     font-size: 28px;
     line-height: 36px;
    }
   }
  }

  .feedback {
   &__name {
    font-size: 16px;
    line-height: 24px;
    max-width: 230px;
   }

   &__text {
    font-size: 15px;
    line-height: 22px;
   }
  }
 }
}

@media screen and (max-width: 479px) {
 .my-cases .case {
  padding: 30px 20px 20px;

  &-top {
   align-items: flex-start;
  }

  &-files {
   &__main {
    .file {
     margin-right: 0;

     &__info {
      &-title {
       font-size: 15px;
       line-height: 22px;
      }

      &-size {
       font-size: 13px;
      }
     }
    }

    &.photo-files {
     flex-wrap: nowrap;
     overflow-y: hidden;
     overflow-x: auto;
     margin-bottom: 10px;

     &::-webkit-scrollbar-track {
      border-radius: 0;
      background-color: transparent;
     }

     &::-webkit-scrollbar {
      width: 0;
      background-color: transparent;
     }

     &::-webkit-scrollbar-thumb {
      border-radius: 0;
      background-color: transparent;
     }
     .photo-file {
      margin-right: 10px;
     }
    }
   }
  }

 }
}
