.modal-card {
 background: rgba(106, 106, 106, .8);
 position: fixed;
 top: 0;
 bottom: 0;
 left: 0;
 right: 0;
 z-index: 10;
 width: 100%;
 height: 100vh;
 overflow: auto;
 &::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: transparent;
 }

 &::-webkit-scrollbar {
  width: 0;
  background-color: transparent;
 }

 &::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-color: transparent;
 }

 &__close {
  text-align: right;
  margin-bottom: 5px;

  img {
   cursor: pointer;
  }
 }

 &__modal {
  max-width: 660px;
  width: 100%;
  max-height: 660px;
  margin: 100px auto;
  text-align: center;
  padding: 26px 26px 60px 85px;
  background: #fafbfe;
  box-shadow: 0 4px 14px 2px rgba(35, 81, 255, 0.06);
  border-radius: 40px;

  .ant-btn {
   padding: 21px 0;
   max-width: 430px;
   width: 100%;
   font-family: Euclid Square;
  }
 }

 &__child {
  margin-right: 50px;
  .modal-text {
   font-size: 22px;
   line-height: 30px;
   margin-top: 20px;
   margin-bottom: 30px;
  }
 }

}

.contact-change,
.report {
 text-align: center;
 margin-right: 60px;
 max-width: 500px;

 &__title {
  font-size: 34px;
  line-height: 40px;
  margin-bottom: 30px;
 }

 &__text {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 50px;
  color: #a3a8bb;
 }

 textarea.ant-input {
  min-height: 200px;
  border: 1px solid #EEF2FF;
  border-radius: 10px;
  resize: none;
  margin-bottom: 60px;
 }

 .ant-btn {
  margin-bottom: 20px;
 }
}

@media screen and (max-width: 991px) {
 .modal-card {
  &__modal {
   margin: 50px auto;
   max-width: 550px;
  }

  .contact-change {
   &__title,
   .report__title {
    font-size: 30px;
    line-height: 36px;
   }

   .ant-btn {
    max-width: 350px;
    margin-bottom: 30px;
    padding: 16px 0;
    font-size: 20px;
    line-height: 24px;
   }
  }
 }
}

@media screen and (max-width: 768px) {
 .modal-card {
  overflow: auto;
  &__modal {
   margin: 50px auto;
   max-width: 440px;
  }

  .contact-change {
   &__title,
   .report__title {
    font-size: 30px;
    line-height: 36px;
   }

   .ant-btn {
    max-width: 350px;
    margin-bottom: 30px;
    padding: 16px 0;
    font-size: 20px;
    line-height: 24px;
   }
  }
 }
}

@media screen and (max-width: 479px) {
 .modal-card {
  &__close {
   display: none;
  }
  &__modal {
   padding: 20px 20px 6px 20px;
   margin: 40px auto;
   max-width: 280px;
   max-height: 490px;
   border-radius: 20px;
  }

  .contact-change,
  .report {
   text-align: left;
   margin-right: 0;
   &__title {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 14px;
   }
   &__text {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 30px;
   }
   textarea.ant-input {
    margin-bottom: 30px;
   }
   .ant-btn {
    max-width: 240px;
    width: 100%;
    margin-bottom: 30px;
    padding: 13px 0;
    font-size: 18px;
    line-height: 23px;
   }
  }

 }
}
