.user-profile {
 margin: 100px 60px 200px;

 .last-active {
  margin-bottom: 66px;
 }

 &-avatar {
  padding-top: 0;
  margin-left: 40px;
  margin-right: 30px;

  &:before {
   left: -20px;
  }
 }

 .card-partner {
  justify-content: flex-start;

  &__info {
   margin-top: 40px;
   display: block;

   &-name {
    margin-bottom: 0;
    cursor: auto;
    &-first {
     margin-right: 10px;
    }
   }

   &-wrapper {
    margin-bottom: 70px;
   }
  }

  &__report {
   color: #a3a8bb;
   margin-left: 70px;
   cursor: pointer;

   img {
    margin-left: 5px;
    margin-bottom: 2px;
   }
  }
 }

 .ant-btn {
  padding: 21px 40px;

  img {
   margin-left: 12px;
  }
 }

}

@media screen and (max-width: 850px) {
 .user-profile .card-partner__info-name {
  font-size: 35px;
  line-height: 45px;
  margin-right: 30px;
  .last-active {
   span {
    font-size: 15px;
   }
  }
 }
}

@media screen and (max-width: 778px) {
 .user-profile {
  margin: 0 0 150px 0;

  &-avatar {
   margin-left: 0;
   margin-right: 40px;

   &:before {
    background: none;
   }

   .avatar {
    .initials {
     font-size: 28px;
     line-height: 40px;
    }
   }
  }

  .card-partner {
   padding: 20px;
   display: flex;

   &__avatar {
    justify-content: flex-start;
   }

   &__info {
    margin-top: 30px;
   }

   &__report {
    margin-left: 40px;
   }
  }

  .ant-btn {
   padding: 14px 30px;
  }
 }
}


@media screen and (max-width: 479px) {
 .user-profile {
  &-avatar {
   margin-right: 0;

   .avatar {
    .initials {
     font-size: 22px;
     line-height: 30px;
    }
   }
  }

  .card-partner {
   min-width: 280px;
   width: 100%;
   padding: 18px;
   display: block;

   &__info {
    margin-top: 15px;

    &-name {
     margin-right: 0;
     margin-left: 5px;
     font-size: 28px;
     line-height: 34px;
    }
   }

   &__report {
    margin-left: 20px;
    font-size: 15px;

    img {
     width: 18px;
    }
   }
  }

  .ant-btn {
   padding: 9px 15px;
   font-size: 15px;
  }
 }
}

@media screen and (max-width: 370px) {
 .user-profile {
  .card-partner {
   &__info {
    &-name {
     font-size: 24px;
     &-first {
      margin-right: 0;
     }
    }
   }
  }
 }
}
