@import '../../utils/style/mixin';

.materials {
 &__info {
  margin: 150px auto 102px;
  text-align: center;

  &-title {
   margin-bottom: 50px;
   .title-main {
    max-width: 700px;
   }
  }

  &-text {
   max-width: 880px;
   width: 100%;
   margin: 0 auto 60px;
   font-size: 20px;
   line-height: 30px;
  }

  &-links {
   margin-left: 50px;
   a {
    border-bottom: 1px dashed #a3a8bb;
    color: #a3a8bb;
    margin-right: 50px;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 30px;
    &:hover {
     color: #2351ff;
    }
   }
  }
 }


 &__videos {
  &-card {
   background: #fff;
   box-shadow: 0 8px 20px 6px rgba(35, 81, 255, 0.06);
   border-radius: 320px;
   margin: 0 60px 60px;
   display: flex;
   align-items: center;
   justify-content: space-between;

   &__video {
    max-width: 640px;
    width: 100%;
    position: relative;
    cursor: pointer;

    &:before {
     content: '';
     position: absolute;
     top: 36%;
     right: 40%;
     background-image: url('../../common/images/materials/play.png');
     background-repeat: no-repeat;
     background-position: center;
     width: 120px;
     height: 123px;
    }

    img {
     border-radius: 530px;
    }
   }

   .card__video {
    &--left {
     margin: 0 100px 0;
    }

    &--right {
     margin: 0 60px 0 210px;
    }
   }
  }
 }

 &__library {
  @include caseLibrary;
 }

 &__faq {
  margin: 0 60px 200px;
  &-title {
   font-size: 46px;
   line-height: 54px;
   margin-bottom: 50px;
  }
 }
}

.faq {
 background: #fff;
 box-shadow: 0 4px 14px 2px rgba(35, 81, 255, 0.06);
 border-radius: 40px;
 padding: 50px 30px 40px 60px;
 margin-bottom: 30px;

 &-top {
  display: flex;
  justify-content: space-between;
  cursor: pointer;

  &__title {
   max-width: 950px;
   width: 100%;
   font-size: 36px;
   line-height: 50px;
  }

  &__arrow {
   background: #edf0fd;
   border-radius: 110px;
   height: 70px;

   .arrow {
    &-up {
     padding: 28px 23px 29px;
    }

    &-down {
     padding: 30px 23px 27px;
    }
   }
  }
 }

 &-text {
  max-width: 1040px;
  width: 100%;
  line-height: 28px;
  margin-bottom: 20px;
 }
}


.card-library {
 background: #fff;
 box-shadow: 0 8px 20px 6px rgba(35, 81, 255, .06);
 border-radius: 40px;
 min-width: 280px;
 max-width: 640px;
 width: 100%;

 &__img {
  position: relative;
  margin-bottom: 30px;

  &-title {
   position: absolute;
   top: 35px;
   left: 40px;
   font-size: 18px;
   padding: 11px 20px 11px 25px;
   background: rgba(255, 255, 255, .8);
   backdrop-filter: blur(18px);
   border-radius: 40px;
  }
 }

 &__info {
  padding: 0 0 0 16px;

  &-category {
   margin-bottom: 10px;
   font-size: 18px;
   line-height: 23px;
   color: #a3a8bb;
  }

  &-title {
   font-size: 46px;
   line-height: 58px;
   margin-bottom: 20px;
  }

  &-text {
   max-width: 560px;
   margin-bottom: 20px;
   font-size: 18px;
   line-height: 28px;
   span {
    cursor: pointer;
    color: #2351ff;
   }
  }
 }

 .ant-card-actions {
  background: rgba(237, 240, 253, .4);
  border-radius: 0 0 40px 40px;
  border-top: none;

  & > li {
   margin: 0;
  }
 }
}

@media screen and (max-width: 1460px) {
 .materials {

 }
 .card-library {
  max-width: 600px;

  .ant-card-cover img {
   max-width: 600px;
  }
 }
}

@media screen and (max-width: 1380px) {
 .card-library {
  max-width: 550px;

  .ant-card-cover img {
   max-width: 550px;
  }
 }
}

@media screen and (max-width: 1280px) {
 .card-library {
  max-width: 640px;

  .ant-card-cover img {
   max-width: 640px;
  }
 }
}

@media screen and (max-width: 991px) {
 /* стили для больших планшетов - начало */
 .faq {
  padding: 50px 30px 40px 60px;
  margin-bottom: 30px;

  &-top {
   margin-bottom: 20px;

   &__title {
    font-size: 24px;
    line-height: 30px;
   }

   &__arrow {
    height: 40px;

    img {
     width: 40px;
    }

    .arrow {
     &-up {
      padding: 14px 10px 0;
     }

     &-down {
      padding: 17px 10px 0;
     }
    }
   }
  }

  &-text {
   line-height: 28px;
   margin-bottom: 20px;
  }
 }
}

@media screen and (max-width: 767px) {
 /* стили для средних планшетов - начало */
 .materials {
  &__info {
   &-title {
    .title {
     &-main {
      font-size: 32px;
      line-height: 42px;
     }
    }
   }

   &-text {
    font-size: 16px;
    line-height: 26px;
   }
  }

  &__library {
   &-title {
    font-size: 44px;
   }

   &-cards {

    .card-library {
     &__img-title {
      top: 25px;
      left: 25px;
      font-size: 15px;
      padding: 5px 10px;
     }

     &__info {
      &-title {
       font-size: 30px;
       line-height: 46px;
      }

      &-text {
       font-size: 15px;
       line-height: 26px;
      }

      &-category {
       font-size: 16px;
       line-height: 20px;
      }
     }

     .download {
      padding: 30px 0;

      svg {
       width: 12px;
       height: 12px;
      }

      &__text {
       font-size: 16px !important;
      }
     }
    }

   }
  }
 }
}

@media screen and (max-width: 479px) {
 /* стили для телефонов - начало */
 .materials {
  margin-bottom: 100px;

  &__info {
   margin: 20px 20px 60px;

   &-title {
    margin-bottom: 20px;

    .title {
     &-top {
      font-size: 16px;
      line-height: 30px;
     }

     &-main {
      font-size: 22px;
      line-height: 26px;
     }
    }
   }

   &-text {
    font-size: 14px;
    line-height: 22px;
    margin: 0 auto 50px;
   }

   &-links {
    margin: 0;

    a {
     font-size: 14px;
     line-height: 30px;
     margin-right: 42px;

     &:last-child {
      margin-right: 0;
     }
    }
   }
  }

  &__library {
   margin: 70px 20px 0;

   &-title {
    font-size: 34px;
    line-height: 43px;
    margin-bottom: 30px;
   }

   &-cards {
    margin: 0 auto 80px;

    .ant-card-body {
     padding: 0 32px 0 0;
    }

    .ant-card {
     margin-right: 0;
     margin-bottom: 40px;
    }

    &__btn {
     .ant-btn {
      font-size: 18px;
      line-height: 23px;
      padding: 13px 95px;
     }
    }

    .card-library {

     &__img-title {
      top: 20px;
      left: 20px;
      line-height: 19px;
     }

     &__info {
      &-title {
       font-size: 28px;
       line-height: 36px;
       margin-bottom: 10px;
      }

      &-text {
       font-size: 14px;
       line-height: 22px;
      }

      &-category {
       font-size: 15px;
       line-height: 22px;
      }
     }

     .download {
      padding: 20px 0;

      svg {
       display: none;
      }

      &__text {
       font-size: 18px !important;
      }
     }
    }
   }
  }

  &__faq {
   margin: 0 20px;

   &-title {
    font-size: 34px;
    line-height: 43px;
    margin-bottom: 30px;
   }

   .faq {
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 20px;

    &-top {
     margin-bottom: 0;

     &__title {
      font-size: 20px;
      line-height: 25px;
     }

     &__arrow {
      height: 20px;
      background: none;

      img {
       width: 35px;
      }
     }
    }

    &-text {
     margin-top: 20px;
     font-size: 14px;
     line-height: 22px;
    }
   }
  }
 }
}
