$black: #232526;
$blue: #2351ff;
$red: #f53a3a;

.main {
 .welcome {
  &__title {
   text-align: center;
   margin: 150px 0 100px;

   .title-main {
    max-width: 930px;
   }
  }

  &__cards {
   display: flex;
   flex-basis: calc(30% - 40px);
   margin: 0 80px 90px;

   &-card {
    min-width: 380px;
    max-width: 400px;
    width: 100%;
    padding: 42px 0 48px 40px;
    background-color: #fff;
    box-shadow: 0 12px 20px 6px rgba(35, 81, 255, 0.06);
    border-radius: 40px;
    font-family: Euclid Flex;

    & + .welcome__cards-card {
     margin-left: 40px;
    }

    &__img {
     margin-bottom: 50px;
     height: 165px;
    }

    &__title {
     color: $blue;
     font-size: 34px;
     line-height: 43px;
     margin-bottom: 10px;
    }

    &__text {
     max-width: 300px;
     width: 100%;
     font-size: 24px;
     line-height: 30px;
     margin-bottom: 20px;
    }
   }
  }

  &__btn {
   margin-bottom: 200px;
   font-family: Euclid Flex;
  }
 }

 .partners {
  width: 100%;
  margin: 0 auto;
  min-height: 1000px;
  padding-top: 200px;
  background-size: contain;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;


  &__img {
   position: absolute;
   top: 0;
   right: 0;
   width: 100%;
   height: 100%;
   overflow: hidden;
   object-fit: cover;
   pointer-events: none;
  }

  &__inner {
   position: relative;
   z-index: 5;
   max-width: 985px;
   width: 100%;
   margin: 0 auto;
  }

  &__title {
   text-align: center;
   margin: 150px 0 100px;
   font-family: Euclid Flex;

   .title-main {
    max-width: 980px;
    width: 100%;
   }
  }

  &__btn {
   margin-bottom: 200px;
   font-family: Euclid Flex;
  }
 }

 .challenge {
  min-height: 930px;

  &__title {
   text-align: center;
   font-family: Euclid Flex;
   margin: 240px 0 100px;
  }

  &__btn {
   margin-bottom: 180px;
   margin-right: 90px;
  }

  &__steps {
   max-width: 1200px;
   margin: 0 auto 100px;
   display: flex;
   align-items: flex-start;
   justify-content: space-between;


   &-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
   }

   &-step {
    max-width: 325px;
    width: 100%;
    text-align: center;
    font-family: 'Euclid Flex';

    &__num {
     font-size: 300px;
     line-height: 300px;
     font-family: 'Euclid Flex';
     background-image: linear-gradient(270deg, #FAFBFE 24.32%, #2351FF 87.82%);
     color: transparent;
     -webkit-background-clip: text;
    }

    &__text {
     font-size: 26px;
     line-height: 30px;
    }
   }

   &-line {
    position: absolute;
    width: 160px;
    left: 124px;
    margin-bottom: 20px;
    padding-top: 2px;
    padding-bottom: 2px;
    background: linear-gradient(270deg, #FAFBFE 12.12%, #2351FF 117.5%);

    &.line2 {
     left: 205px;
    }

    &.line3 {
     left: 195px;
    }
   }
  }
 }

 .users-countries {
  background-image: url('../../common/images/main/transferMap-light.png');
  background-position: center;
  background-size: contain;
  font-family: 'Euclid Flex';
  padding-top: 170px;
  text-align: center;
  margin-bottom: 150px;

  &__num {
   font-size: 240px;
   line-height: 300px;
   font-family: 'Euclid Flex';
   background: linear-gradient(180deg, #FFFFFF -13.16%, #2351FF 91.45%);
   color: transparent;
   -webkit-background-clip: text;
  }

  &__text {
   font-size: 50px;
   line-height: 63px;
   padding-bottom: 300px;
  }
 }

 .feedback-main {
  padding-left: 60px;
  display: flex;
  justify-content: flex-start;

  &__inner {
   margin-left: 110px;
   max-width: 880px;
   width: 100%;

   &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;

    &__info {

     &-name {
      font-family: Euclid Flex;
      font-size: 26px;
      line-height: 33px;
      color: $blue;
      font-weight: 600;
     }

     &-job {
      font-size: 20px;
      line-height: 30px;
     }
    }

    &__btn {
     .btn {
      background: linear-gradient(90deg, #2351FF -0.01%, #FFFFFF 150%);
      padding: 20px 25px 18px;
      border-radius: 50%;
     }
    }
   }

   &-line {
    width: 100%;
    position: relative;
    height: 2px;

    &:after {
     content: '';
     display: inline-block;
     position: absolute;
     left: 0;
     border-bottom: 2px solid $blue;
     opacity: .1;
     width: 100%;
    }

    &:before {
     content: '';
     display: inline-block;
     border-bottom: 2px solid $blue;
     position: absolute;
     left: 0;
     opacity: 1;
     width: 37.5%;
    }
   }

   &-text {
    max-width: 800px;
    width: 100%;
    font-family: Euclid Flex;
    font-size: 36px;
    line-height: 44px;
    padding-top: 30px;
    margin-bottom: 100px;
    padding-right: 20px;
   }

   &-btn {
    font-family: Euclid Flex;
    margin-bottom: 200px;
    text-align: left;
   }
  }
 }
}

@media all and (max-width: 1350px) {
 .main {
  .welcome {
   &__cards {
    display: flex;
    overflow: auto;
    margin: 0;
    padding: 0 20px 90px;

    &::-webkit-scrollbar-track {
     border-radius: 0;
     background-color: transparent;
    }

    &::-webkit-scrollbar {
     width: 0;
     background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
     border-radius: 0;
     background-color: transparent;
    }

    &-card {
     min-width: 380px;
    }
   }
  }
 }
}

@media all and (max-width: 1250px) {
 .slider-switch {
  text-align: center;
  .ant-switch {
   height: 620px;

   &-handle {
    width: 620px;
    height: 618px;
   }

   &-checked .ant-switch-handle {
    left: calc(100% - 622px);
   }
  }
 }
}

@media screen and (max-width: 1091px) {
 .main {
  .challenge {
   &__steps {
    display: block;
    text-align: center;

    &-line {
     display: none;
    }

    &-inner {
     margin-left: 0 !important;
    }

    &-step {
     &__num {
      font-size: 200px;
      line-height: 250px;
      width: auto !important;
     }
    }
   }

   &__btn {
    margin-right: 0;
   }
  }

  .challenge,
  .partners,
  .welcome {
   &__title {
    .title-main {
     font-size: 38px;
    }
   }
  }

  .users-countries {
   &__num {
    font-size: 180px;
    line-height: 280px;
   }

   &__text {
    font-size: 40px;
   }
  }

  .feedback-main {
   padding: 0 20px;

   &__img {
    img {
     width: 250px;
    }
   }

   &__inner {
    margin-left: 40px;
    max-width: 600px;

    &-text {
     font-size: 24px;
     line-height: 36px;
     margin-bottom: 60px;
    }

    &-top {
     &__info {
      &-name {
       font-size: 22px;
       line-height: 28px;
      }

      &-job {
       font-size: 16px;
       line-height: 24px;
      }
     }

     &__btn {
      .btn {
       padding: 9px 17px 13px;

       img {
        width: 18px;
       }
      }
     }
    }

    &-btn {
     .btn {
      padding: 16px 60px;
      font-size: 20px;
      line-height: 28px;
     }
    }
   }
  }
 }
 .slider-switch {
  .ant-switch {
   height: 520px;

   &-handle {
    width: 520px;
    height: 518px;
   }

   &-checked .ant-switch-handle {
    left: calc(100% - 522px);
   }
  }
 }
}

