$black: #232526;
$blue: #2351ff;
$red: #f53a3a;

@import "~slick-carousel/slick/slick";
@import "~slick-carousel/slick/slick-theme";
@import '~antd/dist/antd.css';
@import '~normalize.css';
@import 'utils/style/fonts/fonts';
@import 'utils/style/mixin';
@import 'utils/style/global';

html {
 box-sizing: border-box;
 height: 100%;
 scroll-behavior: smooth;
}

*,
*::after,
*::before {
 box-sizing: inherit;
}

body {
 height: 100%;
 overflow-x: hidden;
 background-color: #fafbfe;
 color: $black;
 font-family: Euclid Square;
 font-style: normal;
 font-weight: 400;
 font-size: 18px;
 line-height: 23px;
}

body,
.ant-checkbox-wrapper {
 -webkit-font-feature-settings: normal;
 -moz-font-feature-settings: normal;
 font-variant: initial;
 font-feature-settings: normal;
 color: #232526;
}
a {
 text-decoration: none;
 color: $black;
}

ul,
li {
 margin: 0;
 padding: 0;
 list-style: none;
}

p {
 padding: 0;
 margin: 0;
}

h1, h2, h3, h4, h5, h6 {
 margin-top: 0;
 margin-bottom: 0;
}

button:focus {
 outline: none;
}

#root {
 height: 100%;
}

.mb12 {
 margin-bottom: 12px;
}

.mb22 {
 margin-bottom: 22px;
}

.mb30 {
 margin-bottom: 30px;
}

.mt20 {
 margin-top: 20px;
}

.mt30 {
 margin-top: 30px;
}

.dn {
 display: none;
}

.border-none {
 border: none;
}

.ml90 {
 margin-left: 90px;
}

.ant-select-item-option {
 padding-left: 35px;
}

.ant-input-group-addon .ant-select-open .ant-select-selector, .ant-input-group-addon .ant-select-focused .ant-select-selector {
 color: #232526;
}

.ant-select-item-option-content {
 flex: none;
}

.ant-form-item-has-error .ant-select:not(.ant-select-disabled):not(.ant-select-customize-input) .ant-select-selector {
 border-color: transparent !important;
}

a:hover {
 color: #2351ff;
}

.cookie {
 position: fixed;
 overflow: hidden;
 box-sizing: border-box;
 z-index: 9999;
 width: 100%;
 display: flex;
 justify-content: space-between;
 align-items: baseline;
 flex-flow: row;
 bottom: 0;
 left: 0;
 right: 0;
 background: #424851;
 color: #fff;
 padding: 1.250em;
 &__text {
  font-size: 14px;
  font-family: "Euclid Flex";
  a {
   color: #2351ff;
  }
 }
 &__btn {
  margin-left: 20px;
  background: #97D058;
  padding: 0.625em 3.125em;
  color: #fff;
  border-radius: 0;
  border: 0;
  font-size: 1em;
  cursor: pointer;
  align-self: center;
  white-space: nowrap;
 }
}

.ant-form-item-explain {
 text-align: right;
 color: #f53a3a;
}

.checkbox-err {
 color: #f53a3a;
}

.app {
 -webkit-font-feature-settings: normal;
 -moz-font-feature-settings: normal;
 font-variant: initial;
 font-feature-settings: normal;
 height: 100%;

 .step-second.mb200 {
  margin-bottom: 200px;
 }

 .flex-start-between {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
 }

 .flex-center {
  display: flex;
  justify-content: center;
 }
 .flex {
  display: flex;
 }

 .flex-align-center {
  display: flex;
  align-items: center;
 }

 .ant-checkbox + span {
  font-size: 18px;
 }

 .ant-select-arrow {
  background-image: url('common/images/partners/down.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  right: 18px;
  width: 16px;

  .anticon {
   display: none;
  }
 }

 .ant-btn:hover, .ant-btn:focus {
  color: $blue;
 }

 .ant-btn {
  height: auto;
  border-radius: 110px;
  padding: 20px 40px;
  font-family: Euclid Flex;
  font-size: 22px;
  line-height: 28px;
  background-color: #edf0fd;
  border: none;

  &.blue {
   color: #fff;
   background: linear-gradient(270.02deg, #FFFFFF -124.54%, #2351FF 111.9%);

   &:disabled {
    background: #a4B7fe;
   }
  }

  &.green {
   color: #fff;
   background: #22B564;
  }

  &.case-red {
   background-color: #fff;
   color: $red;
   border: 1px solid $red;
   &.checked,
   &:hover {
    color: #fff;
    background-color: $red;
   }
  }
  &.case-yellow {
   background-color: #fff;
   color: #EFCD00;
   border: 1px solid #EFCD00;
   &.checked,
   &:hover {
    color: #fff;
    background-color: #EFCD00;
   }
  }
  &.case-green {
   background-color: #fff;
   color: #22B564;
   border: 1px solid #22B564;
   &.checked,
   &:hover {
    color: #fff;
    background-color: #22B564;
   }
  }
 }

 .ant-tooltip {
  display: none;

  &-open {
   display: none;
  }
 }

 .h-auto {
  height: auto;
 }

 .ant-select-single.ant-select-customize-input .ant-select-selector .ant-select-selection-search {
  width: 0;
  padding: 5px 0;
  border-radius: 10px;
 }

 .ant-select-single.ant-select-show-arrow .ant-select-selection-item, .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
  position: static;
  background-color: #fff;
  padding: 8px 11px;
  border-radius: 10px;
 }

 .color-black {
  color: $black;

  a {
   color: $black;
  }
 }

 .ant-switch {
  min-width: 54px;
  height: 30px;

  &:not(.ant-switch-checked) {
   background-color: #E0E2EA;
  }
 }
 .ant-switch-checked {
  background: linear-gradient(269.73deg, #FFFFFF -79.38%, #0035FF 158.67%);
 }
 .ant-switch-checked .ant-switch-handle {
  left: calc(100% - 28px);
 }

 .ant-switch-handle {
  width: 26px;
  height: 26px;

  &:before {
   border-radius: 50%;
  }
 }

 .ant-checkbox {
  border-radius: 6px;
  background: #e0e2ea;
  width: 20px;
  height: 20px;

  &:not(.ant-checkbox-checked) {
   .ant-checkbox-inner {
    background: #e0e2ea;
    width: 20px;
    height: 20px;
   }
  }

  &-inner {
   border-radius: 6px;
   width: 20px;
   height: 20px;

   &:after {
    left: 5.5px;
    width: 5px;
    height: 8px;
   }
  }
 }

 .ant-checkbox-checked .ant-checkbox-inner {
  background-color: $blue;
  border-color: $blue;
 }

 .ant-checkbox-wrapper:hover .ant-checkbox-inner, .ant-checkbox:hover .ant-checkbox-inner, .ant-checkbox-input:focus + .ant-checkbox-inner {
  border-color: transparent;
 }
 .ant-checkbox:hover::after, .ant-checkbox-wrapper:hover .ant-checkbox::after {
  visibility: hidden;
 }
 .download__text {
  color: #a3a8bb;
 }

 .ant-input {
  font-size: 18px;
  line-height: 26px;
 }

 .ant-select-single.ant-select-show-arrow .ant-select-selection-item,
 .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
  font-size: 18px;
  line-height: 26px;
  padding: 16px 11px 18px;
  height: 60px;
  background-color: #fff;
  border: 1px solid #eef2ff;
 }

 .ant-select-single .ant-select-selector {
  max-height: 60px;
 }

 textarea.ant-input {
  &::-webkit-scrollbar-track {
   border-radius: 10px;
   background-color: transparent;
  }

  &::-webkit-scrollbar {
   width: 4px;
   background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
   border-radius: 60px;
   background-color: #D2D5E0;
  }
 }
}

.ant-input {
 -webkit-transition: none;
 transition: none;
}

.ant-input-suffix {
 margin-left: 4px;
 margin-right: 18px;
}

.ant-input:focus, .ant-input-focused {
 border-color: transparent;
 -webkit-box-shadow: none;
 box-shadow: none;
}

.p-textarea {
 padding: 17px 16px 40px 0;
 background-color: #fff;
 border: 1px solid #EEF2FF;
 border-radius: 10px;
}

.ant-select-dropdown {
 border-radius: 20px;
}

.last-active {
 font-size: 18px;
 line-height: 24px;
 color: #a3a8bb;
 margin-bottom: 40px;

 span {
  margin-left: 4px;
  font-size: 18px;
  line-height: 24px;
  color: #232526;
 }
}

.profile,
.layout-auth {
 .ant-form-item-explain.ant-form-item-explain-error {
  color: #f53a3a;
  text-align: right;
 }

 .ant-input-affix-wrapper {
  input {
   border: 1px solid #eef2ff;
  }
 }

 .ant-row {
  display: block;
  max-width: 420px;
  width: 100%;
  margin: 0 auto 12px;
 }

 .ant-col {
  margin: 0 auto;
 }

 .ant-input-affix-wrapper {
  padding: 0;
  border-radius: 10px;
  border: none;

  input {
   padding: 6px 0;
   border: none;
  }
 }

 .ant-form-item-label {
  float: left;

  label {
   font-size: 18px;
   line-height: 28px;
  }

  & > label {
   color: #a3a8bb;
  }
 }

 .ant-input-password-icon {
  margin-right: 12px;

  svg {
   width: 20px;
   height: 20px;
  }
 }

 .border-none {
  .ant-input-wrapper {
   border: none;

   .ant-input-group-addon {
    border-radius: 0;
    border: none;
   }
  }

  .ant-form-item-control-input-content {
   border-radius: 0;
   border: none;

   input {
    border: none;
   }
  }
 }

 .ant-form-item .ant-mentions, .ant-form-item textarea.ant-input {
  min-height: 200px;
  background: #fff;
  border: 1px solid #EEF2FF;
  border-radius: 10px;
  resize: none;


 }

 .ant-input-wrapper {
  border: 1px solid transparent;

  .ant-input-group-addon {
   border-radius: 10px 0 0 10px;
   border: 1px solid transparent;
  }
 }

 .ant-checkbox-wrapper {
  .ant-form-item-control-input-content {
   border: none;
  }
 }

 .ant-form-item-control-input-content {
  border-radius: 10px;
  border: 1px solid #eef2ff;

  input {
   border: none;
  }
 }

 .ant-form-item-label > label::after {
  display: none;
 }

 .ant-form-item-has-error {
  .ant-form-item-control {
   padding-bottom: 0;
  }
 }

 .ant-form-item {
  &-control {
   max-width: 420px;
   width: 100%;
   border-radius: 10px;
   padding-bottom: 24px;
   text-align: left;

   input {
    border-radius: 10px;
    padding: 10px 11px;
   }
  }
 }

 .ant-checkbox-wrapper {
  font-size: 18px;
  line-height: 26px;

  a {
   text-decoration: underline;
  }
 }

 .ant-checkbox-inner {
  background-color: #e0e2ea;
  border-color: #e0e2ea;
  border-radius: 6px;
 }

 .ant-checkbox-checked {
  .ant-checkbox-inner {
   background-color: #2351ff;
   border-color: #2351ff;
   border-radius: 6px;
  }
 }

 .ant-checkbox-wrapper {
  &:hover {
   .ant-checkbox {
    border: none;
   }
  }
 }

 .ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
  display: none;
  content: '';
 }
}

.download {
 display: flex;
 align-items: center;
 justify-content: center;
 padding: 37px 0 38px;

 &__text {
  margin-left: 10px;
 }
}

.card-partner {
 display: flex;
 align-items: flex-start;
 justify-content: space-between;
 padding: 40px 30px 40px 50px;

 &__avatar {
  &-img {
   width: 110px;
   height: 110px;
   background-color: #2351ff;
   border-radius: 50%;
   color: #fff;
   font-size: 44px;
   line-height: 50px;
   display: flex;
   align-items: center;
   justify-content: center;
  }

  .report {
   display: flex;
   align-items: center;
   font-size: 18px;
   line-height: 28px;
   color: #a3a8bb;
   padding-left: 12px;
   margin: 30px auto 0;
   cursor: pointer;
   text-align: center;

   img {
    margin-left: 10px;
   }
  }
 }

 &__info {
  margin-left: 40px;
  width: 100%;

  &-cells {
   max-width: 630px;
  }

  &-name {
   font-size: 44px;
   line-height: 50px;
   margin-bottom: 10px;
  }

  &-btn {
   display: flex;
   align-items: center;
   justify-content: space-between;

   .ant-btn {
    font-size: 22px;
    line-height: 28px;
    padding: 18px 30px 20px 40px;
    border: none;

    img {
     margin-left: 12px;
    }
   }
  }
 }
}

.ant-form-item-has-error .ant-select:not(.ant-select-disabled):not(.ant-select-customize-input) .ant-select-selector {
 background-color: transparent;
}

@media screen and (max-width: 768px) {
 .cookie {
  flex-direction: column;
  &__btn {
   margin-left: 0;
   margin-top: 30px;
  }
 }
}
