@mixin select {
 &__select,
 &-select {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  max-width: 640px;
  width: 100%;

  .ant-select-selection-placeholder {
   background: #edf0fd;
   padding-left: 11px;
  }

  .ant-select-single.ant-select-show-arrow .ant-select-selection-item, .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
   background: #edf0fd;
   border-radius: 20px;
   padding: 8px 20px;
   height: auto;
  }

  &-function {
   padding: 17px 0;
   margin-left: 30px;
  }

  .ant-select {
   font-size: 18px;
   line-height: 26px;

   .ant-select-selector {
    background: #edf0fd;
    border-radius: 20px;
    padding: 8px 100px 8px 0;
    height: auto;
    width: 300px;

    .ant-select-selection-placeholder {
     color: #232526;
     background: #edf0fd;
     height: auto;
    }
   }

   .ant-select-arrow {
    font-size: 16px;
    margin-right: 10px;
   }
  }
 }
}

@mixin caseLibrary {
 margin: 110px 60px 0;

 &-title {
  font-size: 66px;
  line-height: 54px;
  margin-bottom: 60px;
 }

 &-filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 70px;

  &__text {
   max-width: 430px;
   width: 100%;
   font-size: 20px;
   line-height: 30px;
   color: #a3a8bb;
  }

  @include select;

 }

 &-cards {
  margin: 0 auto 170px;
  display: flex;
  flex-basis: calc(45% - 20px);
  align-items: flex-start;
  flex-wrap: wrap;

  .ant-card-cover img {
   max-width: 640px;
   min-width: 280px;
   width: 100%;
  }

  .ant-card-body {
   padding: 0 24px 24px 24px;
  }

  &__btn {
   width: 100%;
   margin: 20px 0 0;
   display: flex;

   .ant-btn {
    margin: 0 auto;
    padding: 18px 94px;
   }
  }

  .ant-card {
   border-radius: 40px;
   margin-bottom: 70px;
   border: none;
   margin-right: 20px;
  }
  .ant-card-bordered .ant-card-cover {
   margin: 0;
  }
 }
}

@mixin textarea {
 textarea {
  font-size: 18px;
  line-height: 30px;

  &::placeholder {
   color: #a3a8bb;
   opacity: .7;
  }

  &.ant-input {
   min-height: 200px;
   max-height: 960px;
   height: auto;
   background: #fff;
   border: none;
   box-sizing: border-box;
   resize: none;
   padding: 10px 36px 10px 25px;
  }
 }
}

@mixin searchAccess {
 .search-access {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__title {
   font-size: 18px;
   line-height: 26px;
  }

  &__text {
   max-width: 334px;
   width: 100%;
   font-size: 15px;
   line-height: 22px;
   color: #a3a8bb;
  }
 }
}
