.user-profile,
.contacts {
 margin-bottom: 100px;

 &__score {
  font-size: 26px;
  line-height: 33px;
  margin-bottom: 30px;

  span {
   color: #2351ff;
  }
 }

 .card {
  margin-bottom: 30px;

  &:last-child {
   margin-bottom: 0;
  }
 }
 .user-profile-not,
 .contacts-not {
  &__text {
   margin-top: 30px;
   margin-bottom: 70px;
  }
 }
 .card-partner__info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;

  &-name {
   cursor: pointer;
   display: flex;
   flex-wrap: wrap;
   align-items: center;

   &.new-contact {
    position: relative;

    &:before {
     content: '';
     background-image: url('../../../common/images/account/newContact.svg');
     background-repeat: no-repeat;
     background-position: center;
     position: absolute;
     left: -22px;
     top: 10px;
     width: 10px;
     height: 10px;
     border-radius: 50%;
    }
   }

   &-first {
    margin-right: 10px;
   }

   &-last {
    position: relative;

    .arrow-up,
    .arrow-down {
     background-repeat: no-repeat;
     background-position: center;
     position: absolute;
     width: 24px;
     height: 12px;
     right: -24px;
     top: 26px;
    }

    .arrow-up {
     background-image: url('../../../common/images/materials/arrowUp.svg');
    }

    .arrow-down {
     background-image: url('../../../common/images/materials/arrowDown.svg');
    }
   }
  }


  &-btn {
   .ant-btn {
    padding: 18px 30px;

    &.btn-cancel {
     padding: 17px 22px 20px;
     background-color: #22B564;

     & + .btn-cancel {
      margin-left: 20px;
      background-color: #edf0fd;
     }
    }

    &.circle {
     padding: 16px 22px;
    }

    img {
     margin: 0;
    }
   }
  }
 }

 &-not {
  &__text {
   font-size: 26px;
   line-height: 33px;
   color: #a3a8bb;
   margin-bottom: 40px;
  }
 }
}

.added-text {
 font-size: 18px;
 line-height: 24px;
 color: #a3a8bb;
}

.btn-status {
 width: 155px;
 text-align: right;

 &__text {
  margin-top: 20px;
  font-size: 18px;
  line-height: 24px;
 }
}

@media screen and (max-width: 1245px) {


 .partners .card,
 .user-profile .card,
 .contacts .card {
  &-partner {
   padding: 30px 30px 30px 40px;

   &__info {
    &-name {
     font-size: 34px;
     line-height: 40px;
     margin-right: 40px;

     &-last {
      .arrow-up,
      .arrow-down {
       width: 18px;
       right: -20px;
       top: 18px;
      }
     }
    }

    .last-active {
     font-size: 18px;
     line-height: 24px;
     margin-bottom: 25px;

     span {
      font-size: 16px;
      line-height: 20px;
     }
    }

    .btn-status {
     width: 135px;

     &__text {
      margin-top: 15px;
      font-size: 16px;
      line-height: 20px;
     }
    }

    &-btn {
     .ant-btn {
      font-size: 18px;
      line-height: 26px;
      padding: 12px 16px 14px;

      &.circle,
      &.btn-cancel {
       padding: 12px 16px 14px;
      }
     }
    }
   }

   &__avatar {
    img {
     width: 90px;
    }
    .avatar {
     width: 90px;
     height: 90px;
    }

    .report {
     margin-top: 25px;
     margin-left: 6px;
     margin-right: 0;

     img {
      width: auto;
     }
    }
   }
  }
 }
}

