@media screen and (max-width: 778px) {
 .card,
 .partners .card,
 .user-profile .card,
 .contacts .card {
  &-partner {
   padding: 20px;
   display: block;

   &__avatar {
    display: flex;
    justify-content: space-between;
    .avatar {
     margin: 0;
    }
    .report {
     margin-top: 0;
     margin-left: 0;
     align-items: flex-start;
     font-size: 15px;
     line-height: 22px;

     img {
      margin-top: 4px;
      margin-left: 7px;
      width: 15px;
     }
    }
   }

   &__info {
    margin-top: 20px;
    margin-left: 0;
    display: block;

    &-name {
     margin-right: 0;

     &.new-contact:before {
      left: -12px;
      top: 12px;
      width: 8px;
      height: 8px;
     }
    }

    &-btn {
     display: block;
    }
   }

   .last-active {
    margin-bottom: 20px;
   }

   &-cell {
    .email-phone {
     &__cell {
      flex-wrap: wrap;
     }

     &__title,
     &__text {
      font-size: 15px;
      line-height: 22px;
     }

     &__text {
      margin-left: 0;
     }
    }

    &__title {
     font-size: 15px;
     line-height: 22px;
    }

    &__text {
     font-size: 15px;
     line-height: 22px;
     .participant,
     .tags {
      &:before {
       content: '\2022';
       position: absolute;
       background: none;
       font-size: 20px;
       top: -2px;
       left: -12px;
       border-radius: 50%;
      }
     }
     .tags:before {
      color: #2351FF;
     }

     .participant:before {
      color: #34C759;
     }
    }
   }
  }
 }
}

@media screen and (max-width: 479px) {

 .user-profile-not,
 .contacts-not {
  text-align: center;
  &__text {
   margin-top: 20px;
   margin-bottom: 70px;
  }
 }

 .partners .card,
 .user-profile .card,
 .contacts {
  &__score {
   display: none;
  }

  .card {
   &-partner {
    margin-right: 20px;

    &__info {
     &-btn {
      margin-right: 0;
      text-align: center;

      .ant-btn {
       padding: 8px 12px 8px;
       font-size: 16px;
       line-height: 24px;

       &.btn-no-add {
        padding: 10px 50px;
       }

       &.btn-cancel {
        padding: 7px 12px 9px;
       }

       &.circle,
       &.btn-cancel-padding {
        padding: 7px 12px 9px 12px;
       }

       img {
        width: 16px;
       }

       .ashbin-text {
        padding: 5px 25px;

        img {
         margin-left: 10px;
        }
       }
      }
     }
    }
   }

   .btn-status {
    width: auto;
    text-align: center;

    &__text {
     font-size: 12px;
     line-height: 15px;
     margin-top: 12px;
    }
   }
  }
 }
}
