.not-found {
 height: 100%;
 color: #fff;
 text-align: center;
 background-image: url('../../common/images/404/bg.jpg');
 background-position: center;
 background-repeat: no-repeat;
 background-color: #2351ff;
 padding: 90px 20px 200px;

 &__title {
  font-family: Euclid Flex;
  font-size: 300px;
  line-height: 380px;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  span {
   background: linear-gradient(270deg, #2351FF 14.69%, #FFFFFF 87.82%);
   color: transparent;
   -webkit-background-clip: text;
  }
 }

 &__subtitle {
  font-size: 44px;
  line-height: 50px;
  color: #fff;
  margin: 0 0 20px;
 }

 &__text {
  font-size: 20px;
  line-height: 30px;
  max-width: 570px;
  width: 100%;
  margin: 0 auto 60px;
 }

 &__btn {
  .ant-btn {
   padding: 21px 133px;
  }

  img {
   margin-left: 14px;
  }
 }
}

@media screen and (max-width: 778px) {
 .not-found {
  background-image: none;
  &__title {
   font-size: 200px;
   line-height: 280px;
  }
  &__subtitle {
   font-size: 36px;
  }
  &__text {
   font-size: 18px;
   max-width: 500px;
  }
  .ant-btn {
   padding: 20px 120px;
   font-size: 20px;
   img {
    display: none;
   }
  }
 }
}

@media screen and (max-width: 550px) {
 .not-found__text {
  max-width: 310px;
 }
}
@media screen and (max-width: 479px) {
 .not-found {
  padding: 25px 20px 160px;
  &__title {
   font-size: 120px;
   line-height: 152px;
  }
  &__subtitle {
   font-size: 28px;
   line-height: 36px;
   margin: 0 auto 10px;
  }
  &__text {
   font-size: 16px;
   line-height: 22px;
   margin: 0 auto 50px;
  }
  .ant-btn {
   padding: 13px 73px;
   font-size: 18px;
   line-height: 23px;
  }
 }
}

@media screen and (max-width: 380px) {
 .not-found__text {
  text-align: center;
  max-width: 280px;
 }
}
