.cases {
 margin: 100px 60px 0;

 &__top {
  &-card {
   background: #fff;
   box-shadow: 0 8px 20px 6px rgba(35, 81, 255, 0.06);
   border-radius: 320px;
   display: flex;
   align-items: center;

   &__img {
    position: relative;

    &-title {
     position: absolute;
     bottom: 25px;
     right: 230px;
     background: rgba(255, 255, 255, 0.8);
     backdrop-filter: blur(18px);
     border-radius: 40px;
     padding: 14px 20px;
     font-size: 18px;
     line-height: 23px;
    }
   }

   &__info {
    margin-left: 70px;

    &-subtitle {
     font-size: 18px;
     line-height: 23px;
     color: #a3a8bb;
    }

    &-title {
     font-size: 70px;
     line-height: 80px;
    }
   }
  }
 }

 &__description {
  margin-top: 140px;

  &-title {
   font-size: 44px;
   line-height: 50px;
   cursor: pointer;
   display: inline-block;

   img {
    margin-left: 16px;
   }
  }

  &-main {
   max-width: 880px;
   width: 100%;
  }
 }

 &__steps {
  font-size: 18px;
  line-height: 24px;
  color: #a3a8bb;

  .steps {

   &-info {
    &__current-step {
     font-size: 44px;
     line-height: 50px;
     color: #232526;
     margin-bottom: 20px;
    }

    &__allotted-date {
     margin-bottom: 10px;
    }

    &__left-date {
     span {
      color: #232526;
     }
    }
   }

   .title-step {
    margin-left: 114px;

    &__title {
     font-size: 44px;
     line-height: 50px;
     color: #2351ff;
     margin-bottom: 20px;
    }

    &__text {
     font-size: 18px;
     line-height: 24px;
     max-width: 730px;
     width: 100%;
    }

    &__child {
     display: flex;
     align-items: center;
     color: #232526;

     a {
      font-size: 18px;
      line-height: 24px;
      border-bottom: 1px solid #232526;
     }

     &-password {
      margin-left: 40px;
      font-size: 18px;
      line-height: 24px;
      color: #a3a8bb;

      span {
       color: #232526;
      }
     }
    }
   }
  }

  .step {
   display: flex;
   border-top: 1px solid rgba(163, 168, 187, .3);
   border-bottom: 1px solid rgba(163, 168, 187, .3);
   padding: 50px 0;
   margin: 50px 0 60px;
  }
 }

 &__case-challenge {
  background: #EDF0FD;
  width: 100%;
  text-align: center;
  padding: 35px 0;
  margin: 0;
  position: relative;

  &:before {
   content: '';
   width: 240px;
   height: 100%;
   background-color: #EDF0FD;
   position: absolute;
   left: -240px;
   top: 0;
  }

  &:after {
   content: '';
   width: 240px;
   height: 100%;
   background-color: #EDF0FD;
   position: absolute;
   right: -240px;
   top: 0;
  }

  a {
   font-size: 24px;
   line-height: 50px;
   margin-right: 190px;
   position: relative;

   &:before {
    content: '';
    background-image: url('../../common/images/account/arrow.svg');
    width: 18px;
    height: 18px;
    position: absolute;
    right: -29px;
    top: 8px;
   }
  }
 }
}

.step-info {
 max-width: 335px;
 width: 100%;

 &__current-step {
  font-size: 44px;
  line-height: 50px;
  color: #232526;
  margin-bottom: 20px;
 }

 &__allotted-date {
  max-width: 283px;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 10px;
 }

 &__left-date {
  font-size: 18px;
  line-height: 24px;

  span {
   color: #232526;
  }
 }
}

.default-case {
 text-align: center;
 margin: 90px 0 160px;

 &__title {
  .title {
   &-top {
    font-size: 22px;
    line-height: 30px;
    margin-bottom: 10px;
   }

   &-main {
    font-size: 46px;
    line-height: 54px;
    max-width: 700px;
    margin-bottom: 90px;
   }
  }
 }
}

@media screen and (max-width: 991px) {
 .default-case {
  &__img {
   img {
    width: 700px;
   }
  }

  &__title {
   .title {
    &-top {
     font-size: 20px;
     line-height: 25px;
    }

    &-main {
     font-size: 38px;
     line-height: 44px;
     max-width: 500px;
     margin-bottom: 70px;
    }
   }
  }
 }
}

@media screen and (max-width: 778px) {
 .default-case {
  &__img {
   img {
    width: 400px;
   }
  }

  &__title {
   .title {
    &-top {
     font-size: 18px;
    }

    &-main {
     font-size: 30px;
     line-height: 40px;
     max-width: 350px;
     margin-bottom: 60px;
    }
   }
  }
 }
}

@media screen and (max-width: 479px) {
 .default-case {
  &__img {
   img {
    width: 280px;
   }
  }

  &__title {
   .title {
    &-top {
     font-size: 16px;
     line-height: 30px;
    }

    &-main {
     font-size: 22px;
     line-height: 28px;
     margin-bottom: 50px;
    }
   }
  }
 }
}
