@import '../../utils/style/mixin';

.case-challenge {
 &__top {
  display: flex;
  align-items: center;
  margin: 100px 60px 125px;

  &-headers {
   margin-left: 70px;

   &__title {
    font-size: 70px;
    line-height: 48px;
    margin-bottom: 30px;
   }

   &__subtitle {
    font-size: 28px;
    line-height: 38px;
   }
  }
 }

 &__main {
  @include caseLibrary;
  @include select;

  &-select {
   margin-bottom: 70px;
   display: flex;
   justify-content: flex-start;
  }
 }
}

.card-library {
 background: #fff;
 box-shadow: 0 8px 20px 6px rgba(35, 81, 255, .06);
 border-radius: 40px;
 max-width: 640px;
 width: 100%;

 &__img {
  position: relative;
  margin-bottom: 30px;

  &-title {
   position: absolute;
   top: 35px;
   left: 40px;
   font-size: 18px;
   padding: 11px 20px 11px 25px;
   background: rgba(255, 255, 255, .8);
   backdrop-filter: blur(18px);
   border-radius: 40px;
  }
 }

 &__info {
  padding: 0 0 0 16px;

  &-category {
   margin-bottom: 10px;
   font-size: 18px;
   line-height: 23px;
   color: #a3a8bb;
  }

  &-title {
   font-size: 46px;
   line-height: 58px;
   margin-bottom: 20px;
  }

  &-text {
   max-width: 560px;
   margin-bottom: 20px;
   font-size: 18px;
   line-height: 28px;
   span {
    cursor: pointer;
    color: #2351ff;
   }
  }
 }

 .ant-card-actions {
  background: rgba(237, 240, 253, .4);
  border-radius: 0 0 40px 40px;
  border-top: none;

  & > li {
   margin: 0;
  }
 }
}
