.account {
 margin: 100px 60px 200px;

 &__top {

  &-avatar {
   position: relative;
   width: 100%;
   max-width: 290px;
   padding-top: 60px;
   margin-left: 40px;
   display: flex;
   align-items: center;

   &:before {
    content: '';
    background-image: url('../../common/images/account/mask-avatar.png');
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    left: -40px;
    width: 100%;
    height: 100%;
   }

   .avatar {
    left: 50px;
    height: 170px;
    width: 170px;
    margin: 0;
    padding: 50px 21px;
    border-radius: 50%;

    .add-photo {
     bottom: 5px;
     right: 20px;
     padding: 17px;
    }
   }
  }

  &-name {
   font-size: 70px;
   line-height: 76px;
   margin-top: 55px;
  }

  &-switch {
   font-size: 18px;
   line-height: 26px;
   margin-left: 335px;
   margin-top: 15px;

   .search-access {
    justify-content: flex-start;

    &__text {
     margin-top: 10px;
     max-width: 584px;
    }

    .ant-switch {
     margin-left: 20px;
    }
   }
  }
 }

 &__main {
  display: flex;
  align-items: flex-start;

  &-menu {
   font-size: 32px;
   line-height: 40px;
   color: #a3a8bb;

   &__lists {
    &-list {
     cursor: pointer;
     margin-bottom: 14px;
     position: relative;

     a {
      color: #a3a8bb;
     }

     &.color-black {
      display: inline-block;

      &:before {
       content: '';
       background-image: url('../../common/images/account/arrow.svg');
       background-repeat: no-repeat;
       width: 18px;
       height: 18px;
       position: absolute;
       right: -30px;
       top: 13px;
      }
     }
    }
   }
  }

  &-wrapper {
   margin-left: 190px;
   width: 100%;

   .profile {
    max-width: 480px;
   }
  }
 }

 .card {
  min-width: 280px;
  max-width: 990px;
  width: 100%;
  background: #fff;
  box-shadow: 0 8px 20px 6px rgba(35, 81, 255, 0.06);
  border-radius: 40px;
  font-size: 28px;
  line-height: 38px;
 }
}

@media screen and (max-width: 991px) {
 /* стили для больших планшетов - начало */
 .account {
  margin: 0 60px;

  &__main {
   display: block;

   &-wrapper {
    margin-left: 0;
   }

   &-menu {
    &__lists {
     display: flex;
     overflow: auto;

     &::-webkit-scrollbar-track {
      border-radius: 0;
      background-color: transparent;
     }

     &::-webkit-scrollbar {
      width: 0;
      background-color: transparent;
     }

     &::-webkit-scrollbar-thumb {
      border-radius: 0;
      background-color: transparent;
     }

     &-list {
      font-size: 24px;
      line-height: 30px;
      margin-right: 20px;

      a {
       display: block;
      }

      &:last-child {
       margin-right: 0;
      }

      &.color-black {
       padding-bottom: 4px;
       border-bottom: 2px solid #2351ff;

       &:before {
        background: none;
       }
      }
     }
    }
   }
  }

  &__top {
   &-avatar {
    max-width: 260px;

    .avatar {
     width: 140px;
     height: 140px;
     left: 40px;

     .add-photo {
      bottom: 0;
      right: 10px;
     }
    }
   }

   &-name {
    font-size: 45px;
    line-height: 58px;
   }
  }

  .ant-btn {
   margin-bottom: 180px;
  }
 }
 .account__top-switch {
  margin-left: 302px;
 }
 .profile .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker,
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item .ant-mentions, .profile .ant-form-item textarea.ant-input, .layout-auth .ant-form-item .ant-mentions, .layout-auth .ant-form-item textarea.ant-input,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  width: 400px;
 }

}

@media screen and (max-width: 810px) {
 /* стили для средних планшетов - начало */
 .profile .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker,
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item .ant-mentions, .profile .ant-form-item textarea.ant-input, .layout-auth .ant-form-item .ant-mentions, .layout-auth .ant-form-item textarea.ant-input,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  width: 340px;
 }
}

@media screen and (max-width: 778px) {
 /* стили для средних планшетов - начало */
 .account {
  margin: 0 auto;
  width: 100%;
  max-width: 470px;
  padding: 0 20px;

  &__top {
   &-avatar {
    max-width: 220px;
    padding-top: 38px;

    .avatar {
     width: 100px;
     height: 100px;
     left: 25px;

     .initials {
      font-size: 35px;
      line-height: 40px;
     }

     .add-photo {
      bottom: -10px;
      right: 30px;
      padding: 15px;
     }
    }
   }

   &-name {
    font-size: 35px;
    line-height: 45px;
   }

   &-switch {
    margin-left: 0;
    margin-bottom: 40px;
   }
  }
 }
}

@media screen and (max-width: 700px) {
 /* стили для телефонов - начало */
 .profile .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker,
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item .ant-mentions, .profile .ant-form-item textarea.ant-input, .layout-auth .ant-form-item .ant-mentions, .layout-auth .ant-form-item textarea.ant-input,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  width: 280px;
 }
}

@media screen and (max-width: 660px) {
 /* стили для телефонов - начало */
 .account {

  &__top {
   &-name {
    margin-top: 30px;
   }

   &-avatar {
    max-width: 150px;
    padding-top: 25px;

    .avatar {
     left: 15px;
    }
   }

   &-switch {
    margin-left: 0;
    margin-bottom: 40px;
   }
  }
 }
 .profile .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker,
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item .ant-mentions, .profile .ant-form-item textarea.ant-input, .layout-auth .ant-form-item .ant-mentions, .layout-auth .ant-form-item textarea.ant-input,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  width: 340px;
 }
}

@media screen and (max-width: 555px) {
 /* стили для телефонов - начало */
 .account {
  &__top-name {
   font-size: 28px;
   line-height: 30px;
  }
 }
 .profile .ant-form-item-control, .layout-auth .ant-form-item-control {
  padding-bottom: 14px;
 }
}

@media screen and (max-width: 479px) {
 /* стили для телефонов - начало */
 .layout-child {
  margin-top: 0;
 }
 .account {
  max-width: 350px;

  &__main {
   margin-top: 35px !important;
   margin-bottom: 100px;

   &-menu {
    &__lists {

     &-list {
      font-size: 16px;
      line-height: 22px;
      margin-right: 20px;
      margin-bottom: 20px;
     }
    }
   }

   &-wrapper {
    margin-left: 0;
    margin-right: 30px;
    .profile {
     margin-bottom: 88px;
    }
   }
  }

  &__top {
   &-avatar, .second-step__wrapper {
    max-width: 75px;
    padding-top: 13px;
    margin-left: 0;

    &:before {
     background: none;
    }

    .avatar {
     padding: 40px;
     width: 90px;
     height: 70px;
     left: 0;

     .add-photo {
      bottom: -12px;
      right: 25px;
      padding: 14px;
     }

     .initials {
      font-size: 20px;
      line-height: 22px;
     }
    }
   }

   &-name {
    font-size: 22px;
    line-height: 28px;
    margin-top: 12px;
    margin-left: 20px;
   }

   &-switch {
    max-width: 327px;
    margin-left: 0;
    margin-top: 0;

    .search-access {
     justify-content: space-between;

     &__text {
      font-size: 14px;
      line-height: 19px;
     }

     &__title {
      font-size: 16px;
     }

     .ant-switch-checked .ant-switch-handle {
      left: calc(100% - 20px);
     }

     .ant-switch {
      min-width: 40px;
      height: 22px;
      margin-right: 20px;

      &-handle {
       width: 18px;
       height: 18px;
      }
     }
    }
   }
  }

  .ant-input {
   font-size: 15px;
   line-height: 19px;
  }

  .ant-checkbox + span {
   padding-right: 0;
   font-size: 14px;
  }

  .ant-btn {
   font-size: 18px;
   line-height: 23px;
   padding: 13px 84px;
   margin-top: 0;
   margin-bottom: 0;
  }
 }

 .ant-select-arrow {
  top: 63%;
 }
 .profile .ant-form-item-label label, .layout-auth .ant-form-item-label label {
  font-size: 16px;
  line-height: 26px;
 }
 .profile .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker,
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  height: 50px;
 }
 .profile .ant-form-item .ant-select, .ant-form-item .ant-cascader-picker,
 .profile .ant-select-single .ant-select-selector,
 .profile .ant-form-item .ant-mentions, .profile .ant-form-item textarea.ant-input, .layout-auth .ant-form-item .ant-mentions, .layout-auth .ant-form-item textarea.ant-input,
 .profile .ant-form-item-control input, .profile .layout-auth .ant-form-item-control input {
  width: 280px;
 }
 .ant-select-dropdown {
  &:not(#register_prefix_list) {
   min-width: 280px !important;
  }
 }
}
