@import "src/utils/style/mixin";

.file {
 display: flex;
 align-items: center;
 margin-bottom: 50px;
 margin-right: 50px;
 width: auto;

 &__info {
  margin-left: 15px;

  &-title {
   font-size: 16px;
   margin: 0;
  }

  &-size {
   font-size: 14px;
   color: #a3a8bb;
  }
 }
}

.select-item {
 border: 1px solid #2351ff;
 background: transparent;
 border-radius: 80px;
 display: flex;
 align-items: center;
 justify-content: space-between;
 padding: 10px 20px 10px 30px;
 margin-bottom: 10px;
 max-width: 325px;
 max-height: 54px;
 margin-right: 8px;
 width: min-content;

 &__drop {
  margin-left: 20px;
  cursor: pointer;
 }

 &__text {
  color: #2351ff;
  font-size: 18px;
  line-height: 32px;
  width: max-content;
 }
}

.case {
 &-text {
  max-width: 820px;
  width: 100%;
  line-height: 28px;
  margin-bottom: 50px;
 }

 &-files {
  &__title {
   font-size: 18px;
   line-height: 24px;
   color: #a3a8bb;
   margin-bottom: 20px;
  }

  &__main {
   display: flex;
   align-items: center;
   justify-content: flex-start;
   flex-basis: calc(33% - 50px);
   flex-wrap: wrap;

   &.photo-files {
    margin-bottom: 50px;
   }

   .photo-file {
    margin-right: 20px;
    border-radius: 20px;
   }
  }
 }
}

.textarea {
 &__title {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 20px;
 }

 @include textarea;

 &__words {
  margin-top: 10px;
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 30px;
  text-align: right;
 }
}

@include searchAccess;


.popup {
 position: fixed;
 top: 0;
 bottom: 0;
 left: 0;
 right: 0;
 z-index: 5;
 width: 100%;
 height: 100vh;
 background: #fafbfe;
 overflow: auto;

 &-container {
  max-width: 1440px;
  width: 100%;
  height: 100vh;
  margin: 11rem auto 50px;
 }

 &__close {
  cursor: pointer;
  margin-top: -100px;
  text-align: center;
  float: right;
  background: #edf0fd;
  border-radius: 110px;
  width: 70px;
  height: 70px;

  img {
   padding: 26px 23px 29px;
  }
 }

 &-slider {
  max-width: 1000px;
  position: relative;
  &-arrow {
   &-right {
    position: absolute;
    top: calc(50% - 50px);
    right: -130px;
    background-image: url('../../common/images/case-challenge/arrow-right.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 150px;
    height: 80px;
    cursor: pointer;
   }

   &-left {
    position: absolute;
    top: calc(50% - 50px);
    left: -130px;
    background-image: url('../../common/images/case-challenge/arrow-left.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 150px;
    height: 80px;
    cursor: pointer;
   }
  }
 }

 &__video {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;

  img {
   border-radius: 20px;
   max-width: 1000px;
   max-height: 555px;
  }

  &-iframe {
   border-radius: 20px;
  }
 }
}
