.avatar {
 position: relative;
 height: 110px;
 width: 110px;
 margin: 0 auto;
 border-radius: 50%;
 &__me-added {
  position: absolute;
  background-color: red;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  color: #fff;
  bottom: -20px;
  right: 2px;

  &--initials {
   bottom: -2px;
  }
 }

 &__inner {
  position: relative;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  overflow: hidden;
 }
}

.profile-pic {
 width: 100%;
}

.upload-button {
 cursor: pointer;
 position: absolute;
 top: 0;
 left: 0;
 height: 100%;
 width: 100%;
 border-radius: 50%;
 overflow: hidden;
}

.file-upload {
 position: absolute;
 opacity: 0;
 top: -9999px;
 left: -9999px;
}

.add-photo {
 position: absolute;
 bottom: 2px;
 right: 4px;
 z-index: 5;
 background-color: #fff;
 box-shadow: 0 8px 20px 6px rgba(35, 81, 255, 0.06);
 border-radius: 40px;
 padding: 17px;
 background-repeat: no-repeat;
 background-position: center;
 width: 22px;
 height: 22px;
 cursor: pointer;
}

.initials {
 font-size: 44px;
 line-height: 50px;
 color: #fff;
 background: rgba(35, 81, 255, 0.9);
 border-radius: 50%;
 height: 100%;
 display: flex;
 justify-content: center;
 align-items: center;
}
